.todo{
    width: 600px;
    min-height: 732px;
    border-radius: 20px;
    background:#e8eee7 ;
    margin: auto;
    margin-top: 174px;
    display: flex;
    flex-direction: column;
    padding: 0px 44px;
    padding-bottom: 30px;
    margin-bottom: 100px; 
}
.todo-header{
    margin-top: 84px;
    color: #002765;
    font-size: 36px;
    font-weight: 600;
    font-style: oblique;
    line-height: 34px;
    display: flex;
    justify-content: center;
}
.todo-header-tag{
    color: #002765;
    font-size: 20px;
    font-weight: 600;
    display: flex;
    justify-content: center;
}
.todo-add{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 45px;

}
.todo-input{
    border-radius: 50px;
    background: #a6a9ad;
    border: none;
    color: #002765;
    font-weight: 600;
    outline: none;
    width: 576px;
    height: 80px;
    padding-left: 35px;
    font-size: 20px;
}
.todo-add-btn{
    border-radius: 50px;
    background: #ff6739;
    width: 187px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: -100px;
    color: #fff;
    font-size: 26px;
    font-weight: 600;
    cursor: pointer;
}